import { useQuery } from "react-query";
import { fetchCoinHistory } from "../api";
import ReactApexChart from "react-apexcharts"

interface IHistorical {
    time_open: string;
    time_close: string;
    open: number;
    high: number;
    low: number;
    close: number;
    volume: number;
    market_cap: number;
  }

interface Idata {
    a: string;
    b: number;
}

interface ChartProps {
    coinId: string;
}

function Price({ coinId }:ChartProps){
    const { isLoading, data } = useQuery<IHistorical[]>(["ohlcv", coinId], () =>
        fetchCoinHistory(coinId),
        {
            refetchInterval: 10000,
        }
    );
    // console.log(data?.map((price) => [
    //                  new Date(price.time_open).getTime(),
    //                  price.open,
    //                  price.high,
    //                  price.low,
    //                  price.close,
    //              ]));
    // console.log(
    //     data?.map((price => price.close))
    // );
    return <div>{isLoading ? "Loading chart..." : 
        <ReactApexChart 
        type="candlestick"
        series={[
            {
              name: "Price",
              data: data?.map((price => price.close)) ?? [],
            },
          ]}
        // series={[
        //   {
        //     data: data?.map((price:{
        //         time_open:any;
        //         open:any;
        //         high:any;
        //         low:any;
        //         close:any;
        //             }) => [
        //             new Date(price.time_open).getTime(),
        //             price.open,
        //             price.high,
        //             price.low,
        //             price.close,
        //         ])??{[][]},
        //   }
        // ]}

            options={{
                plotOptions: {
                    candlestick: {
                    colors: {
                    upward: "#DF7D46", // 상승 시 색상
                    downward: "#3C90EB", // 하락 시 색상
                    },
                    },
                    },
                chart: {
                    height: 500,
                    width: 500,
                    toolbar: {
                        show: false,
                    },
                    background:"transparent",
                },
                title: {
                    text: 'CandleStick Chart',
                    align: 'left'
                },
                xaxis: {
                    type: 'datetime'
                },
                yaxis: {
                    tooltip: {
                      enabled: true
                    }
                }
            }}/>}
            </div>
}

export default Price;